import { gql } from '@apollo/client';

export const LOGIN = gql`
mutation($email : Email ! $password :  String !){
    globalLogin(data: {
      email : $email
      password : $password
    }){
      BearerTokenResponse{
        access_token
      }
      user{
        name
      }
      Status{
        check
        code
        message
      }
    }
  }
`;
export const SEND_PIN_CODE = gql`
mutation($email : Email ! $sendPinCodeFor :  sendPinCodeFor !){
  globalsendPinCode(data:{
    email : $email
    sendPinCodeFor : $sendPinCodeFor
  }){
    Status{
      code
      message
      check
    }
  }
}
`;
export const CREATE_ACCOUNT = gql`
  mutation($name : Name! , $password : Password ! $phone : Name! $user_type_id : Int !  $email : Email ! $pin_code : String!){
    globalRegister(data:{
      newUserInput :{
        name : $name,
        password : $password,
        phone : $phone,
        user_type_id : $user_type_id,
      },
      verifyPinCodeInput:{
        email : $email,
        pin_code : $pin_code
      }
    }){
      BearerTokenResponse{
        access_token
      }
      user{
        name
      }
      Status{
        code 
        message
        check
      }
    }
  }
`
export const VERIFY_CODE= gql`
mutation($email :  Email ! $pin_code : String!){
  globalverifyPinCode(data: {
    email : $email
    pin_code : $pin_code
  }){
    Status{
      code 
      message
      check
    }
  }
}`

export const RESET_PASSWORD = gql`
mutation($password : Password! $email : Email ! , $pin_code : String !){
  globalPasswordresets(data: {
    forgotPasswordInput : {
      password : $password
    },
    verifyPinCodeInput : {
      email : $email
      pin_code : $pin_code
    }
  }){
    BearerTokenResponse{
      access_token
    }
    user{
      name
    }
    Status{
      code
      message
      check
    }
  }
}`



