import React from 'react';
import { useMutation } from '@apollo/client';
import Loader from 'react-loader-spinner';

import ReactCodeInput from 'react-verification-code-input';
import Main from './main.component';
import ResetPassword from './reset-password.component';
import { SEND_PIN_CODE, VERIFY_CODE } from '../../apis/userAccount';
import text from '../../languages/english/translate.json';

export default function VerifyPinCode(props) {

    const [values, setValues] = React.useState({
        showResetPassword: false,
        showVerify: true,
        errors: {
            form: ''
        }
    });
    const [verifyCode, loading] = useMutation(VERIFY_CODE);
    const [sendPinCode, loadingResend] = useMutation(SEND_PIN_CODE);

    const getCodeFronInputs = () => {
        let code = "";
        let inputs = document.querySelectorAll('input');

        for (var x = 0; x < inputs.length; x++) {
            code = code + inputs[x].value
        }

        return code;
    }
    const handleVerify = () => {
        let code = getCodeFronInputs();
        if (code.length === 5) {
            setValues({...values , code : code})
            verifyCode({
                variables: {
                    email: props.data.email,
                    pin_code: code
                }
            }).then(res => {
                if (res.data.globalverifyPinCode.Status.check === true) {
                    handleOnSuccess(code);
                }
                else {
                    handleOnFaild(res.data.globalverifyPinCode.Status.message);
                }
            })
        }
    }
    const handleChange = () => {
        resetData();
    }
    const handleOnSuccess = (code) => {
        if (props.type === 'Register') {
            props.openRegistration(code);
        }
        else {
            setValues({ ...values, showResetPassword: true, showVerify: false , code : code })
        }
    }
    const handleOnFaild = (message) => {
        // when email and code not matched
        let errors = values.errors;
        errors.form = message;
        setValues({ ...values, errors: errors })
    }
    const handleResendCode = () => {
        resetData();
        sendPinCode({
            variables: {
                email: props.data.email,
                sendPinCodeFor: props.type
            }
        }).then(res => {
            if (res.data.globalsendPinCode.Status.code === 200) {
                handleSuccessResend()
            }
            else {
                handleOnFaild(res.data.globalsendPinCode.Status.message)
            }
        });
    }
    const resetData = () => {
        let errors = values.errors;
        errors.form = '';
        errors.success = '';
        setValues({ ...values, errors: errors })
    }
    const handleSuccessResend = () => {
        let errors = values.errors;
        errors.success = text.validation.codeSent;
        setValues({ ...values, errors: errors })
    }

    return (
        <>
            {loading.loading === true || loadingResend.loading === true &&
                <div className="loader">
                    <Loader
                        type="Circles"
                        color="white"
                        height={50}
                        width={50}
                    />
                </div>
            }
            {values.showVerify &&
                <Main
                    title="Email confirmation"
                    description={`Please write down the 5 digit coda that was sent to : ${props.data.email}`}
                    button="Verify"
                    buttonFunc={handleVerify}
                    bottomNav="Resend"
                    bottomFunc={handleResendCode}
                    content={
                        <>
                            {values.errors.success &&
                                <p className="text-success mt-2">{values.errors.success}</p>
                            }
                            <ReactCodeInput
                                fields={5}
                                fieldHeight={45}
                                fieldWidth={69}
                                className="w-auto cutom-pincode text-center mb-2"
                                onComplete={handleVerify}
                                onChange={handleChange}
                            />
                            {values.errors.form &&
                                <p className="text-danger mt-2">{values.errors.form}</p>
                            }
                        </>
                    }

                />
            }
            {values.showResetPassword &&
            <ResetPassword email={props.data.email} code={getCodeFronInputs()}/>
            }
        </>
    )
}