import React , {useState} from 'react';
import ScrollIntoView from 'react-scroll-into-view';

import BlueNav from './blue-nav.component';
import Registration from '../sign-in/registration.component';
import Login from '../sign-in/login.component';
import Index from '../sign-in/index';

// styles and images
import { Navbar, Nav } from 'react-bootstrap';
import logoWeb from '../../images/logos/eDallal-logo.svg';


export default function HomeNav() {
    const [showLogin , setShowLogin ] = useState(false);
    const [showRegister , setShowRegister] = useState(false);
    
    const hanldecloseRegister = () => setShowRegister(false);
    const hanldecloseLogin = () => setShowLogin(false);

    return(
        <>
            <BlueNav />
            <Navbar collapseOnSelect sticky="top" expand="lg" bg="white" className="p-0 shadow" variant="light">
                {/* for web */}
                <Navbar.Brand className="d-web pl-2">
                    <img src={logoWeb} className="img-fluid" alt="logo" />
                </Navbar.Brand>
                {/*  for mob */}
                <Navbar.Brand className="d-mob p-1">
                    <img src={logoWeb} className="" alt="logo" />
                </Navbar.Brand>
                {/* buttons for mobile */}
                <Nav className="d-mob">
                    <button className="btn-white  mr-1 " onClick={e=>setShowRegister(true)}>Register</button>
                    <button className="btn-blue"  onClick={e=>setShowLogin(true)}>Sign In</button>
                </Nav>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto p-0 ">
                        <ScrollIntoView selector="#header">
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                Home
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector="#about">
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                About us
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector="#services">
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                Services
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector="#collaborators">
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                Collaborators
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector="#contact">
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                Contact Us
                            </Nav.Link>
                        </ScrollIntoView>
                    </Nav>
                </Navbar.Collapse>
                {/* buttons for web */}
                <Nav className="ml-auto inline-block d-web bg-lightblue" style={{ padding: '0.5rem 0.75rem' }}>
                    <button
                        className="btn-white mr-2 ml-2"
                        onClick={e=>setShowRegister(true)}>
                        Create an account</button>
                    <button
                        className="btn-blue mr-2 ml-2 pr-5 pl-5"
                        onClick={e=>setShowLogin(true)}
                    >Login</button>
                </Nav>
            </Navbar>
            
            <Index show={showRegister} close={hanldecloseRegister} content={<Registration />}/>
            <Index show={showLogin} close={hanldecloseLogin} content={<Login />}/>

        </>
    )
}