import {Modal} from 'react-bootstrap';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';

export default function ProcessDone(props){

    return(
        <Modal.Body  className="pr-3 pl-3" >
            <main className="mt-4 mb-3 text-blue w-100 text-center">
                <h3 className="mb-3 d-inline-block">{props.title}</h3>
                <figure className="w-100 text-center">
                    <CheckCircleOutlineIcon style={{fontSize : '3.5rem'}} className="text-success"/>
                </figure>
            </main>
         </Modal.Body>
    )
}