import { Modal } from 'react-bootstrap';

export default function Main(props) {
    return (
        <>
            <Modal.Body className="pt-4 pr-4 pl-4" >
                <header>
                    <h5 className="font-weight-bold">{props.title}</h5>
                    <p className="text-grey">{props.description}</p>
                </header>
                <main className="mt-4">
                    {props.content}
                </main>
            </Modal.Body>
            <Modal.Footer className="border-0 pr-4 pl-4">
                <button
                    onClick={props.buttonFunc}
                    className="btn-blue w-100 pt-2 pb-2  mr-0 ml-0"
                >{props.button}</button>
                <article className="w-100 text-center">
                    <p className="text-grey d-inline-block mb-0">{props.bottomText}</p>
                    <button
                        onClick={props.bottomFunc}
                        className="d-inline-block border-0 bg-transparent text-blue"
                    >{props.bottomNav}</button>
                </article>
            </Modal.Footer>
        </>
    )
}