import React from 'react';
import Loader from 'react-loader-spinner';

import Main from './main.component';
import ProcessDone from './process-done.component';
import { password, matchPassword, required, validateForm } from '../../validations/validation';
import { useMutation } from '@apollo/client';
import {RESET_PASSWORD} from '../../apis/userAccount'

import { FormControl, InputLabel, OutlinedInput, InputAdornment, IconButton } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

export default function Register(props) {
    const [values, setValues] = React.useState({
        password: '',
        showPassword: false,
        showConfirmPassword: false,
        showSuccess : false , 
        showReset : true,
        errors: {
            password: '',
            confirmPassword: ''
        }
    });
    const [resetPassword, loading] = useMutation(RESET_PASSWORD);

    // password and confirm password hide and show
    const handleClickShowPassword = (props) => (event) => {
        setValues({ ...values, [props]: !values[props] });
    };
    // make input focus after show or hide password
    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };
    const handleChange = (props) => (event) => {

        setValues({ ...values, [props]: event.target.value });
        let errors = values.errors;
        errors.confirmPassword = ''
        switch (props) {
            case 'password':
                errors.password = password(event.target.value)
                break;
            case 'confirmPassword':
                errors.confirmPassword = password(event.target.value)
                break;
            default:
                break;
        }
    };
    const CheckRequired = () => {

        let errors = values.errors;
        errors.password = !values.password ? required(values.password) : values.password;
        errors.confirmPassword =  !values.confirmPassword ? required(values.confirmPassword) : values.confirmPassword;

        if (values.password && values.confirmPassword) {
            errors.confirmPassword = matchPassword(values.password, values.confirmPassword);
        }

        setValues({ ...values, errors: errors })
    }
    const handleSubmit = () => {
        CheckRequired();
        if (validateForm(values.errors)) {
            resetPassword({
                variables: {
                    password : values.password,
                    email : props.email,
                    pin_code : props.code
                }
            }).then(res => {
                if (res.data.globalPasswordresets.Status.check === true) {
                    handleOnSuccess();
                }
                else{
                    handleOnFaild(res.data.globalPasswordresets.Status)
                }
            });
        }
        else {
            console.log("not valid");
        }
    }
    const handleOnSuccess = () => {
        setValues({ ...values, showSuccess : true , showReset : false});
    }
    const handleOnFaild = (message) => {
        let errors = values.errors;
        errors.form = message;
         setValues({ ...values, errors: errors });
    }
    return (
        <>
        {loading.loading === true  &&
                <div className="loader">
                    <Loader
                        type="Circles"
                        color="white"
                        height={50}
                        width={50}
                    />
                </div>
        }
        {values.showReset &&
            <Main
                title="Reset your password"
                description="Please enter your new password"
                button="Reset password"
                buttonFunc={handleSubmit}
                content={
                    <>
                        <FormControl
                            required
                            className="mb-1"
                            variant="outlined"
                            fullWidth
                            size="small"
                        >
                            <InputLabel htmlFor="password">Password</InputLabel>
                            <OutlinedInput
                                id="password"
                                type={values.showPassword ? 'text' : 'password'}
                                value={values.password}
                                onChange={handleChange('password')}
                                labelWidth={120}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword('showPassword')}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                        >
                                            {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <p className="text-danger">{values.errors.password}</p>
                        <FormControl
                            variant="outlined"
                            fullWidth
                            size="small"
                            className="mb-1"
                        >
                            <InputLabel htmlFor="Confirmpassword">Confirm password</InputLabel>
                            <OutlinedInput
                                id="Confirmpassword"
                                type={values.showConfirmPassword ? 'text' : 'password'}
                                value={values.confirmPassword}
                                onChange={handleChange('confirmPassword')}
                                labelWidth={180}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword('showConfirmPassword')}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                        >
                                            {values.showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <p className="text-danger">{values.errors.confirmPassword}</p>
                        {values.errors.form &&
                            <p className="text-danger">{values.errors.form}</p>
                        }
                    </>
                }
            />
        }
        {values.showSuccess &&
            <ProcessDone title="Your password reset successfully"/>
        }
        </>
    )
}