import React from 'react';
import { useMutation } from '@apollo/client';
import Loader from 'react-loader-spinner';

import Main from './main.component';
import CheckEmail from './check-email.component';
import ProcessDone from './process-done.component';
import Registration from './registration.component';
import { email, validateForm, required } from '../../validations/validation';
import { LOGIN } from '../../apis/userAccount';
import text from '../../languages/english/translate.json';

import { TextField, FormControl, InputLabel, OutlinedInput, InputAdornment, IconButton, FormControlLabel, Checkbox } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

export default function Register(props) {
    const [login, loading] = useMutation(LOGIN);

    const [values, setValues] = React.useState({
        password: '',
        showPassword: false,
        showConfirmPassword: false,
        showCheckMail: false,
        showLogin: true,
        rememberMe: true,
        showSuccess : false , 
        showRegistration : false ,
        errors: {
            email: '',
            password: ''
        }
    });
    // password and confirm password hide and show
    const handleClickShowPassword = () => {
        setValues({ ...values, showPassword: !values.showPassword });
    };
    // make input focus after show or hide password
    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };
    const handleChange = (props) => (event) => {
        if (props === 'rememberMe') {
            setValues({ ...values, [props]: event.target.checked });
        }
        else {
            setValues({ ...values, [props]: event.target.value });
        }
        let errors = values.errors;
        errors.form = '';

        switch (props) {
            case 'email':
                errors.email = email(event.target.value)
                break;
            default:
                break;
        }
    };
    const CheckRequired = () => {
        let errors = values.errors;
        errors.email = !values.email ?  required(values.email) : values.errors;
        errors.password = !values.password ?  required(values.password) : values.password;

        setValues({ ...values, errors: errors })
    }
    const handleSubmit = () => {
        CheckRequired();

        if (validateForm(values.errors)) {
            login({
                variables: {
                    email: values.email,
                    password: values.password
                }
            })
                .then(res => {
                    if (res.data.globalLogin.Status.check === true) {
                        handleOnSuccess();
                    }
                    else{
                        handleOnFail(res.data.globalLogin.Status.message);
                    }
                });
        }
        else {
            console.log("form is not valid!")
        }
    }
    const handleOnSuccess = () => {
        setValues({ ...values, showSuccess: true , showLogin : false });
    }
    const handleOnFail = (message) => {
        let errors = values.errors;
        errors.form = message;
        setValues({ ...values, errors: errors })
    }
    const handleForgitPassword = () => {
        setValues({ ...values, showCheckMail: true, showLogin: false });
    }
    const handleShowRegistration = () => {
        setValues({ ...values, showRegistration: true, showLogin: false });
    }
    return (
        <>
            {loading.loading === true &&
                <div className="loader">
                    <Loader
                        type="Circles"
                        color="white"
                        height={50}
                        width={50}
                    />
                </div>
            }
            {values.showLogin &&
                <Main
                    title="Welcome Back"
                    description="Please provide your mail and password to procceed"
                    button="login"
                    buttonFunc={handleSubmit}
                    bottomText="Dont have an account ? "
                    bottomNav="Create new"
                    bottomFunc ={handleShowRegistration}
                    content={
                        <>
                            <TextField
                                fullWidth
                                size="small"
                                type="email"
                                className="mb-1"
                                id="outlined-disabled"
                                label="Email Address"
                                value={values.email}
                                onChange={handleChange('email')}
                                variant="outlined"
                            />
                            <p className="text-danger">{values.errors.email}</p>
                            <FormControl
                                className="mb-1"
                                variant="outlined"
                                fullWidth
                                size="small"
                            >
                                <InputLabel htmlFor="password">Password</InputLabel>
                                <OutlinedInput
                                    id="password"
                                    type={values.showPassword ? 'text' : 'password'}
                                    value={values.password}
                                    onChange={handleChange('password')}
                                    labelWidth={70}
                                    endAdornment={
                                        <InputAdornment position="end">
                                            <IconButton
                                                aria-label="toggle password visibility"
                                                onClick={handleClickShowPassword}
                                                onMouseDown={handleMouseDownPassword}
                                                edge="end"
                                            >
                                                {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                        </InputAdornment>
                                    }
                                />
                            </FormControl>
                            {values.errors.form &&
                                <p className="text-danger">{values.errors.form}</p>
                            }
                            {values.errors.success &&
                                <p className="text-blue">{values.errors.success}</p>
                            }
                            <article className="row justify-content-between align-items-center m-0">
                                <span className="col-md-5 col-6 p-0 ">
                                    <FormControlLabel
                                        className="text-left checkBox-cutom-label"
                                        control={<Checkbox checked={values.rememberMe} onChange={handleChange('rememberMe')} name="rememberMe" />}
                                        label="Remember me"
                                    />
                                </span>
                                <span className="col-md-4 col-5 p-0 text-right ">
                                    <button
                                        style={{ fontSize: '12px' }}
                                        className=" pb-2 border-0 bg-transparent"
                                        onClick={handleForgitPassword}
                                    >Forgot Password ?</button>
                                </span>
                            </article>
                        </>
                    }
                />
            }
            {values.showCheckMail &&
                <CheckEmail />
            }
            {values.showSuccess &&
                <ProcessDone title="You logged in successfully"/>
            }
            {values.showRegistration &&
                <Registration />
            }
        </>
    )
}