import React, { useEffect } from 'react';
import Loader from 'react-loader-spinner';
import { useQuery, useMutation } from '@apollo/client';

import { USER_TYPES } from '../../apis/userData';
import { SEND_PIN_CODE, CREATE_ACCOUNT } from '../../apis/userAccount';
import VerifyPinCode from './verify-pincode.component';
import Login from './login.component';
import Main from './main.component';
import ProcessDone from './process-done.component';
import { name, password, phone, email, required, validateForm, matchPassword } from '../../validations/validation';
import text from '../../languages/english/translate.json';

// styles icons and images
import { TextField, FormControl, InputLabel, OutlinedInput, InputAdornment, IconButton, FormLabel, FormControlLabel, Radio, RadioGroup } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

export default function Registration() {
    const [values, setValues] = React.useState({
        code : '',
        name: '',
        password: '',
        showPassword: false,
        showConfirmPassword: false,
        userType: 1,
        showVerifyCode: false,
        showRegistration : true ,
        showSuccess : false , 
        showLogin : false,
        errors: {
            name: '',
            email: '',
            password: '',
            confirmPassword: '',
            phone: ''
        }
    });
    const { loading, error, data } = useQuery(USER_TYPES);
    const [sendPinCode, loadingSubmit] = useMutation(SEND_PIN_CODE);
    const [createAccount, loadingCreate] = useMutation(CREATE_ACCOUNT);
    console.log(loading);
    useEffect(() => {
        data && setValues({ ...values, userTypes: data.UserTypes })
        if (error) {
            let errors = values.errors;
            errors.form = error;
            setValues({ ...values, errors: errors })
        }
    }, [data]);

    // password and confirm password hide and show
    const handleClickShowPassword = (props) => (event) => {
        setValues({ ...values, [props]: !values[props] });
    };
    // make input focus after show or hide password
    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };
    const handleChange = (props) => (event) => {
        if (props === 'userType') {
            setValues({ ...values, [props]: parseInt(event.target.value) });
        }
        else {
            setValues({ ...values, [props]: event.target.value });
        }
        let errors = values.errors;
        errors.form = ''
        switch (props) {
            case 'name':
                errors.name = name(event.target.value)
                break;
            case 'email':
                errors.email = email(event.target.value)
                break;
            case 'password':
                errors.password = password(event.target.value)
                break;
            case 'confirmPassword':
                errors.confirmPassword = password(event.target.value)
                break;
            case 'phone':
                errors.phone = phone(event.target.value)
                break;
            default:
                break;
        }
    };
    const CheckRequired = () => {

        let errors = values.errors;
        errors.name = !values.name ? required(values.name) : errors.name;
        errors.email = !values.email ? required(values.email) : errors.email;
        errors.password = !values.password ? required(values.password) : !errors.password ;
        errors.confirmPassword = !values.confirmPassword ? required(values.confirmPassword) : errors.confirmPassword;
        errors.phone = !values.phone ? required(values.phone) : errors.phone;

        if (values.password && values.confirmPassword) {
            errors.confirmPassword = matchPassword(values.password, values.confirmPassword);
        }

        setValues({ ...values, errors: errors })
    }
    const handleSubmit = () => {
        CheckRequired();
        if (validateForm(values.errors)) {
            if(values.code){
                handleCompleteCreateAcc(values.code)
            }
            else
            {
                handleSendPinCode()
            }
        }
        else {
            console.log("not valid");
        }
    }
    const handleSendPinCode = () => {
        sendPinCode({
            variables: {
                email: values.email,
                sendPinCodeFor: "Register"
            }
        }).then(res => {
            if (res.data.globalsendPinCode.Status.check === true) {
                handleOnSuccess();
            }
            else{
                handleOnFaild(res.data.globalsendPinCode.Status.message);
            }
        });
    }
    const handleOnSuccess = () => {
        setValues({ ...values, showVerifyCode: true , showRegistration : false});
    }
    const handleOnFaild = (message) => {
        let errors = values.errors;
        errors.form = message;
        setValues({ ...values, errors: errors });
    }
    const handleCreateAccount = (code) => {
        setValues({...values , showRegistration : true , showVerifyCode : false , code : code})
        createAccount({
            variables: {
                name: values.name,
                password: values.password,
                phone: values.phone,
                user_type_id: values.userType,
                email: values.email,
                pin_code: code
            }
        }).then(res => {
            if (res.data.globalRegister.Status.check === true) {
                handleOnSuccessCreateAccount();
            }
            else{
                handleOnFaildCreateAccount(res.data.globalRegister.Status)
            }
        });
    }
    const handleCompleteCreateAcc = (code) => {
         handleCreateAccount(code)
    }
    const handleOnSuccessCreateAccount = () => {
        setValues({ ...values, showSuccess: true , showRegistration : false  , showVerifyCode : false});
    }
    const handleOnFaildCreateAccount = (status) => {
        let errors = values.errors;
        // name is exists
        if(status.code === 501){
            errors.name = status.message;
        }// phone number is exists
        else if(status.code === 502){
            errors.number = status.message;
        }
        //email is exists
        else if(status.code === 503){
            errors.email = status.message;
        }
        setValues({ ...values, errors: errors , showRegistration : true  , showVerifyCode : false , showSuccess: false});

    }
    const handleShowLogin = () => {
        setValues({ ...values, showLogin: true , showRegistration : false});
    }
    console.log(values.name);
    return (
        <>
        {(loading || loadingSubmit.loading || loadingCreate.loading) && 
            <div className="loader">
                <Loader
                    type="Circles"
                    color="white"
                    height={50}
                    width={50}
                />
            </div>
        }
        {values.showRegistration &&
            <Main
                title="Create new account"
                description="Please complete the following data to complete your profile"
                button="Create an account"
                buttonFunc={handleSubmit}
                bottomText="Have an account ? "
                bottomNav="Login"
                bottomFunc={handleShowLogin}
                content={
                    <>
                        <TextField
                            fullWidth
                            required
                            size="small"
                            className="mb-1"
                            id="outlined-disabled"
                            label="User name"
                            variant="outlined"
                            value={values.name}
                            onChange={handleChange('name')}
                        />
                        <p className="text-danger">{values.errors.name}</p>
                        <TextField
                            required
                            fullWidth
                            size="small"
                            type="email"
                            className="mb-1"
                            id="outlined-disabled"
                            label="Email Address"
                            value={values.email}
                            onChange={handleChange('email')}
                            variant="outlined"
                        />
                        <p className="text-danger">{values.errors.email}</p>
                        <FormControl
                            required
                            className="mb-1"
                            variant="outlined"
                            fullWidth
                            size="small"
                        >
                            <InputLabel htmlFor="password">Password</InputLabel>
                            <OutlinedInput
                                id="password"
                                type={values.showPassword ? 'text' : 'password'}
                                value={values.password}
                                onChange={handleChange('password')}
                                labelWidth={80}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword('showPassword')}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                        >
                                            {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <p className="text-danger">{values.errors.password}</p>
                        <FormControl
                            fullWidth
                            required
                            variant="outlined"
                            size="small"
                            className="mb-1"
                        >
                            <InputLabel htmlFor="Confirmpassword">Confirm Password</InputLabel>
                            <OutlinedInput
                                id="Confirmpassword"
                                type={values.showConfirmPassword ? 'text' : 'password'}
                                value={values.confirmPassword}
                                onChange={handleChange('confirmPassword')}
                                labelWidth={140}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword('showConfirmPassword')}
                                            onMouseDown={handleMouseDownPassword}
                                            edge="end"
                                        >
                                            {values.showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        </FormControl>
                        <p className="text-danger">{values.errors.confirmPassword}</p>
                        <TextField
                            required
                            fullWidth
                            size="small"
                            type="tel"
                            className="mb-1"
                            id="outlined-disabled"
                            label="Phone number"
                            variant="outlined"
                            value={values.phone}
                            onChange={handleChange('phone')}
                        />
                        <p className="text-danger">{values.errors.phone}</p>
                        <FormControl fullWidth component="fieldset">
                            <FormLabel className="mb-1" component="legend">Register as:</FormLabel>
                            <RadioGroup color="primary" aria-label="gender" name="gender1" value={values.userType} onChange={handleChange('userType')}>
                                <div className="row m-0">
                                    {values.userTypes?.map(type =>
                                        <FormControlLabel
                                            value={type.id}
                                            className="col-md-3 pl-0 col-5 mb-0"
                                            control={<Radio />} label={type.name} />
                                    )}
                                </div>
                            </RadioGroup>
                        </FormControl>
                        {values.errors.form &&
                            <p className="text-danger">{values.errors.form}</p>
                        }
                    </>
                }
            />
        }
        {values.showVerifyCode &&
             <VerifyPinCode 
                type="Register"
                data={values} 
                openRegistration={handleCompleteCreateAcc}/>}
        {values.showSuccess &&
            <ProcessDone title="Your Account created successfully"/>
        }
        {values.showLogin &&
            <Login />
        }
        </>
    )
}