import React from 'react';
import Loader from 'react-loader-spinner';
import { useMutation } from '@apollo/client';

import Main from './main.component';
import { email, validateForm, required } from '../../validations/validation';
import { SEND_PIN_CODE } from '../../apis/userAccount';
import VerifyPinCode from './verify-pincode.component';

import { TextField } from '@material-ui/core';

export default function CheckEmail(props) {
    const [values, setValues] = React.useState({
        email: '',
        showVerifyCode: false,
        showCheckEmail: true,
        errors: {
            email: ''
        }
    });
    const [sendPinCode, loading] = useMutation(SEND_PIN_CODE);
    const handleChange = (props) => (event) => {

        setValues({ ...values, [props]: event.target.value });
        let errors = values.errors;

        switch (props) {
            case 'email':
                errors.email = email(event.target.value)
                break;
            default:
                break;
        }
    };
    const CheckRequired = () => {

        let errors = values.errors;
        errors.email = !values.email ?  required(values.email) : values.errors;

        setValues({ ...values, errors: errors })
    }
    const handleSubmit = () => {
        CheckRequired();
        if (validateForm(values.errors)) {
            handleSendCode();
        }
        else {
            console.log("form is not valid!")
        }
    }
    const handleSendCode = () => {
        sendPinCode({
            variables: {
                email: values.email,
                sendPinCodeFor: "passwordresets"
            }
        }).then(res => {
            console.log(res)
            if (res.data.globalsendPinCode.Status.check === true) {
                handleOnSuccess();
            }
            else if (res.data.globalsendPinCode.Status.check === false) {
                handleOnFaild(res.data.globalsendPinCode.Status.message);
            }
        });
    }
    const handleOnSuccess = () => {
        handleCloseCheckMail();
        setValues({ ...values, showVerifyCode: true, showCheckEmail: false });
    }
    const handleOnFaild = (message) => {
        let errors = values.errors;
        errors.email = message;
        setValues({...values , errors: errors })
    }
    const handleCloseCheckMail = () => {
        let errors = values.errors;
        errors.email = '';

        setValues({ errors: errors })
    }
    const handleCloseVerifyCode = () => setValues({ ...values, showVerifyCode: false });
    return (
        <>
            {loading.loading === true &&
                <div className="loader">
                    <Loader
                        type="Circles"
                        color="white"
                        height={50}
                        width={50}
                    />
                </div>
            }
            {values.showCheckEmail &&
                <Main
                    title="Reset your password"
                    description="Please enter your email address and we will send you a confirmation mail to your inbox."
                    button="Continue"
                    buttonFunc={handleSubmit}
                    content={
                        <>
                            <TextField
                                fullWidth
                                size="small"
                                type="email"
                                className=" mt-1"
                                id="outlined-disabled"
                                label="Email address"
                                value={values.email}
                                onChange={handleChange('email')}
                                variant="outlined"
                            />
                            <p className="text-danger">{values.errors.email}</p>

                        </>
                    }
                />
            }
            { values.showVerifyCode &&
                <VerifyPinCode
                    data={values}
                    type="passwordresets"
                />
            }
        </>
    )
}